/* Full-page background */
.chart-root {
  min-height: 100vh;
  padding: 24px;
  background: radial-gradient(circle at top, #1f2937 0, #020617 55%, #000 100%);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  box-sizing: border-box;
}

/* Main shell */
.chart-shell {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  background: linear-gradient(145deg, rgba(15, 23, 42, 0.96), rgba(15, 23, 42, 0.85));
  border-radius: 24px;
  padding: 24px 24px 20px;
  box-shadow:
    0 18px 60px rgba(15, 23, 42, 0.85),
    0 0 0 1px rgba(148, 163, 184, 0.18);
  backdrop-filter: blur(26px);
  border: 1px solid rgba(148, 163, 184, 0.2);
}

/* Header */
.chart-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  margin-bottom: 16px;
}

.chart-title {
  font-size: 24px;
  margin: 0;
  color: #e5e7eb;
  letter-spacing: 0.04em;
}

.chart-subtitle {
  margin: 4px 0 0;
  font-size: 13px;
  color: #9ca3af;
}

.chart-header-right {
  display: flex;
  gap: 8px;
  align-items: center;
}

/* Chips */
.chip {
  padding: 6px 12px;
  border-radius: 999px;
  font-size: 12px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.chip-gradient {
  background: linear-gradient(135deg, #22c55e, #16a3ff);
  color: #0b1120;
  font-weight: 600;
}

.chip-soft {
  background: rgba(15, 23, 42, 0.8);
  border: 1px solid rgba(148, 163, 184, 0.5);
  color: #e5e7eb;
}

/* Toolbar */
.chart-toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
  gap: 12px;
}

.timeframe-group {
  display: inline-flex;
  padding: 4px;
  border-radius: 999px;
  background: rgba(15, 23, 42, 0.9);
  border: 1px solid rgba(148, 163, 184, 0.35);
}

.tf-btn {
  border: none;
  background: transparent;
  color: #9ca3af;
  padding: 6px 14px;
  border-radius: 999px;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.18s ease-out;
}

.tf-btn:hover {
  color: #e5e7eb;
  background: rgba(55, 65, 81, 0.6);
}

.tf-btn-active {
  background: linear-gradient(135deg, #22c55e, #16a3ff);
  color: #020617;
  font-weight: 600;
  box-shadow: 0 0 0 1px rgba(15, 23, 42, 0.4);
}

.toolbar-right {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Refresh button */
.icon-btn {
  border: none;
  border-radius: 999px;
  width: 32px;
  height: 32px;
  font-size: 18px;
  line-height: 1;
  background: rgba(15, 23, 42, 0.9);
  color: #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 0 0 1px rgba(148, 163, 184, 0.5);
  transition: all 0.18s ease-out;
}

.icon-btn:hover {
  transform: translateY(-1px) rotate(8deg);
  box-shadow:
    0 8px 20px rgba(15, 23, 42, 0.7),
    0 0 0 1px rgba(56, 189, 248, 0.7);
}

/* Tiny pulse indicator when loading */
.pulse-dot {
  width: 8px;
  height: 8px;
  border-radius: 999px;
  background: #22c55e;
  box-shadow: 0 0 0 0 rgba(34, 197, 94, 0.7);
  animation: pulse 1.2s infinite;
}

/* Chart card */
.chart-card {
  position: relative;
  background: radial-gradient(circle at top, #020617, #020617);
  border-radius: 18px;
  padding: 8px 8px 4px;
  box-shadow: inset 0 0 0 1px rgba(31, 41, 55, 0.9);
  overflow: hidden;
}

/* Loading overlay */
.chart-loading-overlay {
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at top, rgba(15, 23, 42, 0.9), rgba(15, 23, 42, 0.98));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  z-index: 10;
  color: #e5e7eb;
  font-size: 13px;
}

/* Spinner */
.spinner {
  width: 26px;
  height: 26px;
  border-radius: 999px;
  border: 3px solid rgba(148, 163, 184, 0.4);
  border-top-color: #22c55e;
  animation: spin 0.8s linear infinite;
}

/* Action buttons */
.actions-row {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 16px;
}

.action-btn {
  flex: 1 1 0;
  min-width: 140px;
  border-radius: 999px;
  border: none;
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  color: #e5e7eb;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.18s ease-out;
  position: relative;
  overflow: hidden;
}

.action-btn::before {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at top, rgba(255, 255, 255, 0.16), transparent 55%);
  opacity: 0;
  transition: opacity 0.18s ease-out;
}

.action-btn:hover::before {
  opacity: 1;
}

.action-btn:hover {
  transform: translateY(-1px) scale(1.01);
  box-shadow: 0 10px 28px rgba(15, 23, 42, 0.85);
}

.action-btn:disabled {
  opacity: 0.7;
  cursor: default;
  transform: none;
  box-shadow: none;
}

.action-fvg {
  background: linear-gradient(135deg, #22c55e, #16a3ff);
  color: #020617;
}

.action-bull {
  background: linear-gradient(135deg, #4ade80, #22c55e);
}

.action-bear {
  background: linear-gradient(135deg, #f97373, #ef4444);
}

/* subtle "working" animation */
.action-btn-loading {
  animation: pulse-soft 1.1s infinite;
}

/* Result banner */
.result-banner {
  margin-top: 14px;
  padding: 10px 14px;
  border-radius: 14px;
  background: rgba(15, 23, 42, 0.95);
  border: 1px solid rgba(34, 197, 94, 0.35);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  font-size: 13px;
  color: #e5e7eb;
}

.result-label {
  text-transform: uppercase;
  letter-spacing: 0.12em;
  font-size: 11px;
  color: #a5b4fc;
}

.result-text {
  font-weight: 500;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px) scale(0.99);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(16px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(34, 197, 94, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(34, 197, 94, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(34, 197, 94, 0);
  }
}

@keyframes pulse-soft {
  0% {
    transform: translateY(-1px) scale(1);
  }
  50% {
    transform: translateY(-2px) scale(1.02);
  }
  100% {
    transform: translateY(-1px) scale(1);
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Utility animation classes */
.animate-fade-in {
  animation: fadeIn 0.6s ease-out;
}

.animate-slide-up {
  animation: slideUp 0.5s ease-out;
}

.animate-slide-up-delayed {
  animation: slideUp 0.5s ease-out 0.1s backwards;
}

/* Responsive tweaks */
@media (max-width: 768px) {
  .chart-shell {
    padding: 18px 16px 16px;
  }

  .chart-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .chart-title {
    font-size: 20px;
  }

  .actions-row {
    flex-direction: column;
  }
}
